.contact-container {
  width: 100%;
  text-align: center;
  position: relative;

  background-color: #101010;
  /* background-color: #36454f; */
  height: calc(100vh - 60px);
  color: white;
  /* user-select: none; */
}

.contact-text {
  border-radius: 5px;
  display: inline-block;
  width: 700px;
  height: 150px;
  margin-top: 50px;
  position: relative;
  font-size: 7em;
  font-family: "Poligon", sans-serif;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.email-redirect {
  border-radius: 5px;
  margin: auto;
  margin-top: 110px;
  color: black;
  text-decoration: none; /* no underline */
  padding: 10px 0px;
  max-width: 150px;
  max-height: 50px;
}

.email-redirect:hover {
  opacity: 80%;
  cursor: pointer;
}

.email-redirect a {
  /* display: inline; */
  background-color: #2b2a2a;
  color: white;
  text-decoration: none; /* no underline */
  border: 1px solid rgb(255, 215, 137);
  border-radius: 5px;
  width: 100%;
  height: 100%;
  padding: 15px;
  font-size: 1.5em;
}
